import { INPUT_BOX_STYLES } from "assets/base-theme";
import InputError from "components/InputError/InputError";
import Label from "components/Label/Label";
import { IBaseInputProps } from "components/base/BaseInput/BaseInput";
import { IInputCommonStyleProps } from "components/interface/Input";
import { InputText } from "primereact/inputtext";
import React from "react";
import { useTranslation } from "react-i18next";
import { generateClasses } from "utils/Util";
//import BaseInputError from "../BaseInputError/BaseInputError";

interface IInputBoxProps extends IBaseInputProps, IInputCommonStyleProps {
  img?: string;
  imgAlt?: string;
  iconClassName?: string;
}

const IconInput: React.FC<IInputBoxProps> = (props: any) => {
  const getClassNames = () => {
    const styleObject = {
      ...INPUT_BOX_STYLES,
      overrideStyle: props.styleObj,
      overrideClasses: props.className,
      isDisabled: props.disabled,
      isInvalid: props.validation ? true : false,
      removeStyleProperty: [],
      removeClasses: [],
    };

    return generateClasses(styleObject, props.variant);
  };
  const {
    value,
    style,
    hideLabel,
    hideErrorRow,
    onChange,
    name,
    placeholder,
    label,
    validation,
    hideError,
    labelClassNames,
    labelStyleObj,
    errorClassNames,
    dataField,
    img,
    imgAlt,
    onFocus,
    onKeyDown,
    onClick,
    iconClassName,
  } = props;

  const { t } = useTranslation();
  const translatedPlaceholder = t(placeholder);
  const translatedImgAlt = t(imgAlt);

  return (
    <>
      {hideLabel && (
        <Label
          label={label}
          labelClassNames={labelClassNames}
          labelStyleObj={labelStyleObj}
          isInvalid={validation}
        />
      )}
      <div className="relative">
        <InputText
          className={getClassNames()}
          value={value}
          style={style}
          onChange={onChange}
          name={name}
          placeholder={translatedPlaceholder}
          data-field={dataField}
          onFocus={onFocus}
          onKeyDown={onKeyDown}
        />
        <div
          className={
            iconClassName +
            " absolute w-9 h-9 top-2 right-0 flex justify-center items-center cursor-pointer text-xxs "
          }
          onClick={onClick}
        >
          <img src={img} alt={translatedImgAlt} />
        </div>
      </div>
      {!hideError && (
        <InputError
          validation={validation}
          hideErrorRow={hideErrorRow}
          errorClassNames={errorClassNames}
        />
      )}
    </>
  );
};

export default IconInput;
